import "./nullstyle.scss";
import "./App.scss";
import Select from "react-select";
import { useState } from "react";
import { useIMask } from "react-imask";

function App() {
  const [isDone, setIsDone] = useState(false);
  const [student, setStudent] = useState("");
  const [cabinetLink, setCabinetLink] = useState("");
  const [phoneMaskOpt, setPhoneMaskOpt] = useState({ mask: "+{7}(000)000-00-00" });
  const [grade, setGrade] = useState("");
  const [email, setEmail] = useState("");
  const [institution, setInstitution] = useState({ value: "", label: "" });
  const schoolOptions = [
    {
      value: " 32",
      label: " 32",
    },
    {
      value: " 24",
      label: " 24",
    },
    {
      value: " 14",
      label: " 14",
    },
    {
      value: " 57",
      label: " 57",
    },
    {
      value: "  ",
      label: "  ",
    },
  ];
  const [submitError, setSubmitError] = useState("");

  const { ref: phoneInputRef, value: phone, setValue: setPhone } = useIMask(phoneMaskOpt);

  const submit = async (e) => {
    e.preventDefault();
    const requireInputs = [...document.querySelectorAll(`._req`)];
    const emailRegexp = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    const result = [...requireInputs].filter((item) => {
      if (item.value) {
        return true;
      }
      if (item.textContent && item.textContent !== " ...") {
        return true;
      }
    });
    if (result.length !== requireInputs.length) {
      setSubmitError("   !");
      return;
    }
    if (phone.length < 16) {
      setSubmitError("  !");
      return;
    }
    if (!emailRegexp.test(email)) {
      setSubmitError("  !");
      return;
    }
    const studentObject = {
      student,
      cabinetLink,
      grade,
      phone,
      email,
      institution: institution.value,
    };
    const response = await fetch("https://forma-5ba33-default-rtdb.europe-west1.firebasedatabase.app/students.json", {
      method: "POST",
      body: JSON.stringify(studentObject),
    });
    if (response.ok) {
      setIsDone(true);
    } else {
      setSubmitError("-   ,  !");
    }
  };

  return !isDone ? (
    <div className="container">
      <h1 className="heading"> </h1>
      <form noValidate onSubmit={submit} className="form">
        <Select
          onChange={setInstitution}
          className="req-star _req my-select-container"
          classNamePrefix={"my-select"}
          placeholder={" ..."}
          options={schoolOptions}
        />
        <label className="input-label">
          <div className="input-wrapper req-star">
            <input
              placeholder="   "
              className="input _req"
              value={grade}
              onChange={(e) => {
                setGrade(e.target.value);
              }}
              onFocus={() => {
                setSubmitError("");
              }}
              type="text"
            />
          </div>
        </label>
        <label className="input-label">
          <div className="input-wrapper req-star">
            <input
              placeholder=" "
              className="input _req"
              value={student}
              onChange={(e) => {
                setStudent(e.target.value);
              }}
              onFocus={() => {
                setSubmitError("");
              }}
              type="text"
            />
          </div>
        </label>
        <label className="input-label">
          <div className="input-wrapper req-star">
            <input
              placeholder=" "
              className="input _req"
              value={email}
              onChange={(e) => {
                setEmail(e.target.value);
              }}
              onFocus={() => {
                setSubmitError("");
              }}
              type="text"
            />
          </div>
        </label>
        <label className="input-label">
          <div className="input-wrapper req-star">
            <input
              ref={phoneInputRef}
              placeholder=" "
              className="input _req"
              value={phone}
              onChange={(e) => {
                setPhone(e.target.value);
              }}
              onFocus={() => {
                setSubmitError("");
              }}
              type="text"
            />
          </div>
        </label>
        <label className="input-label">
          <div className="input-wrapper">
            <input
              placeholder="    "
              className="input"
              value={cabinetLink}
              onChange={(e) => {
                setCabinetLink(e.target.value);
              }}
              onFocus={() => {
                setSubmitError("");
              }}
              type="text"
            />
          </div>
        </label>

        {submitError ? <div className="form__error">{submitError}</div> : false}
        <button className="submit-btn" type="submit">
           
        </button>
      </form>
    </div>
  ) : (
    <h1 className="heading">!</h1>
  );
}

export default App;
